import styles from './TaskWorkflow.less';
import { Row, Col, Steps, Divider, Progress, Modal } from 'antd';
import React, { useState, useEffect } from 'react';
import { RESOURCE_AGENT_STATUS_TIMESTAMP_STORAGE_KEY, RESOURCE_AGENT_STATUS_STORAGE_KEY } from '@/contexts/constants';
import { RightCircleOutlined, ApiOutlined, OneToOneOutlined, AppstoreOutlined, InteractionOutlined } from '@ant-design/icons';
import MainGraph from './Graph/main';

const displayCom = (current_step) => {

    const desStatus = (step_n) => {
        // 小于step等待，等于进行中，大于完成
        if (current_step >= 10) return "finish";
        return current_step < step_n ? 'wait' : current_step === step_n ? "process" : 'finish'
    }

    return (
        [
            {
                title: 'Start',
                status: desStatus(6),
                icon: <RightCircleOutlined />,
            },
            {
                title: 'Create Computing',
                status: desStatus(7),
                icon: <ApiOutlined />,
            },
            {
                title: 'Create Network',
                status: desStatus(8),
                icon: <OneToOneOutlined />,
            },
            {
                title: 'Deploy Application',
                status: desStatus(9),
                icon: <AppstoreOutlined />,
            },
            {
                title: 'Complete',
                status: desStatus(10),
                icon: <InteractionOutlined />,
            },
        ]
    )
};

const DisplayOrch = ({ current_step }) => {

    let cfProgress = 0;
    let netProgress = 0;
    let appProgress = 0;
    const [cfProgressS, setCfProgress] = useState(0);
    const [netProgressS, setNetProgress] = useState(0);
    const [appProgressS, setAppProgress] = useState(0);

    useEffect(() => {
        // 创建一个新的定时器，在每一秒钟执行一次函数来更新时间状态。
        const timerIdxxx = setInterval(() => {
            if (current_step === 7) {
                cfProgress = cfProgress + 30;
                if (cfProgress >= 100) {
                    cfProgress = 100;
                }
                setCfProgress(cfProgress);
            }
            if (current_step === 8) {
                netProgress = netProgress + 30;
                if (netProgress >= 100) {
                    netProgress = 100;
                }
                setNetProgress(netProgress);
            }
            if (current_step === 9) {
                appProgress = appProgress + 30;
                if (appProgress >= 100) {
                    appProgress = 100;
                }
                setAppProgress(appProgress);
            }
        }, 1000);

        // 清除定时器以防止内存泄漏。
        return () => clearInterval(timerIdxxx);
    }, [current_step]);

    return (
        <>
            <Row className={styles.orch}>
                <Col span={8}>
                    Create Computing
                </Col>
                <Col span={8}>
                    Node Name: NX Computing
                </Col>
                <Col span={8}>
                    Progress: <Progress percent={cfProgressS} />
                </Col>
            </Row>
            <Row className={styles.orch}>
                <Col span={8}>
                    Create Network
                </Col>
                <Col span={8}>
                    Path: Beijing-Hebei-Shanxi-Shanxi-Ningxia
                </Col>
                <Col span={8}>
                    Progress: <Progress percent={netProgressS} />
                </Col>
            </Row>
            <Row className={styles.orch}>
                <Col span={8}>
                    Deploy Application
                </Col>
                <Col span={8}>

                </Col>
                <Col span={8}>
                    Progress: <Progress percent={appProgressS} />
                </Col>
            </Row>
        </>
    )
};

const displayInteractionLogs = (current_step, showDetail) => {

    const step_map = new Map(JSON.parse(localStorage.getItem(RESOURCE_AGENT_STATUS_TIMESTAMP_STORAGE_KEY)));
    const one = {
        title: <div>{step_map.get('5')}</div>,
        description: (
            <div>Start <a onClick={() => showDetail('5')}>Detail</a></div>
        ),
    };
    const two = {
        title: <div>{step_map.get('7')}</div>,
        description: (
            <div>Compute Power Activation Begins
                {/* <a onClick={() => showDetail('7')}>Detail</a> */}
            </div>
        ),
    };
    const thre = {
        title: <div>{step_map.get('8')}</div>,
        description: (
            <div>Network Activation Begins
                {/* <a onClick={() => showDetail('8')}>Detail</a> */}
            </div>
        ),
    };
    const fo = {
        title: <div>{step_map.get('9')}</div>,
        description: (
            <div>Application Deployment Begins
                {/* <a onClick={() => showDetail('9')}>Detail</a> */}
            </div>
        ),
    };
    let temp = parseInt(current_step)
    if (temp < 6) {
        return [one]
    }
    else if (temp === 6) {
        return [one]
    }
    else if (temp === 7) {
        return [one, two]
    }
    else if (temp === 8) {
        return [one, two, thre]
    }
    else if (temp === 9) {
        return [one, two, thre, fo]
    } else if (temp > 9) {
        return [one, two, thre, fo]
    }
};

const TaskWorkflow = ({ readyStatus, setReadyStatus }) => {


    let initConut = parseInt(localStorage.getItem(RESOURCE_AGENT_STATUS_STORAGE_KEY))
    const [count, setCount] = useState(initConut + 1);

    const totalExecutions = 9;
    // 进来的值从local中拿
    let executionCount = initConut + 1;

    useEffect(() => {
        // 创建一个新的定时器，在每一秒钟执行一次函数来更新时间状态。
        const timerId = setInterval(() => {

            // 如果大于9直接不定时走了
            if (executionCount >= totalExecutions) {
                clearInterval(timerId);
            }

            executionCount++;

            // 放key
            setCount(executionCount);
            localStorage.setItem(RESOURCE_AGENT_STATUS_STORAGE_KEY, executionCount);
            setReadyStatus(executionCount);

            // 拿出来时间继续往里放
            let statusTimestamps = new Map();
            const statusTimestampsStr = localStorage.getItem(RESOURCE_AGENT_STATUS_TIMESTAMP_STORAGE_KEY);
            if (statusTimestampsStr) {
                statusTimestamps = new Map(JSON.parse(statusTimestampsStr));
            }
            // 往里放值
            statusTimestamps.set(executionCount.toString(), new Date().toLocaleString());
            localStorage.setItem(RESOURCE_AGENT_STATUS_TIMESTAMP_STORAGE_KEY, JSON.stringify(Array.from(statusTimestamps.entries())));

        }, 5000);

        // 清除定时器以防止内存泄漏。
        return () => clearInterval(timerId);
    }, []);

    const [isModalVisible, setIsModalVisible] = useState(false);
    const [logF, setLogF] = useState('');

    const handleCancel = () => {
        setIsModalVisible(false);
    };

    const handleOK = (logflag) => {
        setIsModalVisible(true);
        setLogF(logflag);
    };

    return (
        <div>
            <Row>
                <Col span={12} style={{ overflowY: "scroll" }} className={styles.container}>
                    <div>
                        <h3>Orchestration Process</h3>
                        <Steps
                            labelPlacement="vertical"
                            size="small"
                            current={parseInt(count)}
                            items={displayCom(count)}
                        />
                    </div>
                    <Divider />
                    <div>
                        <h3>Orchestration Progress and Details</h3>
                        <DisplayOrch current_step={parseInt(count)} />
                    </div>
                    <Divider />
                    <div>
                        <h3>Interaction Logs</h3>
                        <Steps
                            progressDot
                            size='small'
                            current={parseInt(count)}
                            direction="vertical"
                            items={displayInteractionLogs(count, handleOK)}
                        />
                        <Modal
                            title="log detail"
                            open={isModalVisible}
                            footer={null}
                            onCancel={handleCancel}
                            width={"80%"}
                        >
                            <pre>
                                {
                                    `
@prefix : <http://example.com/#> .
@prefix rdf: <http://www.w3.org/1999/02/22-rdf-syntax-ns#> .
@prefix rdfs: <http://www.w3.org/2000/01/rdf-schema#> .
@prefix xsd: <http://www.w3.org/2001/XMLSchema#> .
@prefix tmf: <http://www.tmforum.org/ns/tmf#> .
@prefix intent: <http://www.tmforum.org/ns/intent#> .

:MedicalImagingAnalysisBusiness rdf:type intent:BusinessIntent ;
    intent:hasName "AI Medical Imaging Analysis Application Service" ;
    intent:hasGoal [
        rdf:Description 
        intent:hasObjective [
            rdf:Description 
            intent:hasRequirement [
                rdf:Description 
                intent:hasResourceType "5G Medical Private Network" ;
                intent:hasLocation "Qingxiu District, Qingshan City" ;
                intent:hasPerformance [
                    rdf:Description 
                    intent:hasBandwidth "≥1 Gbps"^^xsd:string ;
                    intent:hasLatency "<10 ms"^^xsd:string ;
                    intent:hasPacketLoss "<0.01%"^^xsd:string 
                ]
            ] ;
            intent:hasRequirement [
                rdf:Description 
                intent:hasResourceType "Edge Cloud Resource Pool" ;
                intent:hasLocation "XX Green Data Center, Qingshan City" ;
                intent:hasPerformance [
                    rdf:Description 
                    intent:hasCPU "64-core"^^xsd:string ;
                    intent:hasGPU "8×NVIDIA A100"^^xsd:string ;
                    intent:hasStorage "200 TB Encrypted SSD (DICOM-compatible)"^^xsd:string ;
                    intent:hasThroughput "≥100 images/min (training)"^^xsd:string 
                ]
            ] ;
            intent:hasRequirement [
                rdf:Description 
                intent:hasResourceType "AI Model Service" ;
                intent:hasCapability "Lung Nodule/Liver Lesion AI Model (NMPA-certified)"^^xsd:string ;
                intent:hasFeature "Support model fine-tuning with local data"^^xsd:string 
            ]
        ]
    ] ;
    intent:hasConstraint [
        rdf:Description 
        intent:hasConstraint [
            rdf:Description 
            intent:hasRequirement [
                rdf:Description 
                intent:hasResourceType "Green Data Center" ;
                intent:hasAttribute "PUE ≤1.3"^^xsd:string ;
                intent:hasEnergySaving "Intelligent power-saving technology (nighttime power reduction)"^^xsd:string 
            ]
        ] ;
        intent:hasConstraint [
            rdf:Description 
            intent:hasRequirement [
                rdf:Description 
                intent:hasResourceType "Healthcare Data Security" ;
                intent:hasCompliance "《Healthcare Data Security Guidelines》"^^xsd:string ;
                intent:hasSecurity "AES-256 encryption + blockchain evidence storage"^^xsd:string 
            ]
        ]
    ] ;
    intent:hasOutcome [
        rdf:Description 
        intent:hasEffect [
            rdf:Description 
            intent:hasObjective [
                rdf:Description 
                intent:hasEffect "Achieve 99.9% business availability"^^xsd:string 
            ]
        ] ;
        intent:hasEffect [
            rdf:Description 
            intent:hasObjective [
                rdf:Description 
                intent:hasEffect "Reduce first-year CAPEX by $122,800"^^xsd:string 
            ]
        ] ;
        intent:hasEffect [
            rdf:Description 
            intent:hasObjective [
                rdf:Description 
                intent:hasEffect "Cut annual labor costs by $224,000–$364,000"^^xsd:string 
            ]
        ]
    ] .
`
                                }
                            </pre>
                        </Modal>
                    </div>
                </Col>
                <Col span={12}>
                    <div>
                        <h3>Network and Computing Orchestration Process</h3>
                        <MainGraph current_step={parseInt(count)} />
                    </div>
                </Col>
            </Row>
        </div>
    );
}

export default TaskWorkflow;